.statContainer {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, 256px);
    justify-content: space-between;
    grid-column-gap: 5px;
    grid-row-gap: 40px;
}

@media screen and (max-width: 984px) {
    .statContainer {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
}