.header {
  width: 100%;
  height: 80px;
  padding: 0 9.7222222222%;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 23px 0px rgba(0, 0, 0, 0.35);
  position: absolute;
}

.header__wrap {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  min-width: 100px;
  margin: 0 20px 0 0;
  color: #000;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-variant: all-small-caps;
}

.header__logoHighlight {
  margin: 0;
  color: #0077FF;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-variant: all-small-caps;
}

.header__menuAndContactsBlock {
  max-width: 969px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 92px;
}

@media screen and (max-width: 984px) {
  .header {
    padding: 0 74px;
  }
  .header__menuAndContactsBlock {
    flex-direction: row-reverse;
  }
}
@media screen and (max-width: 568px) {
  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
  }
  .header__wrap {
    width: 100%;
  }
}/*# sourceMappingURL=Header.css.map */